.content-wrap {
    margin: 0 auto;
  }
  
  .side {
    padding-top: 3em;
    padding-bottom: 40px;
    float: left;
    width: 300px;
    position: fixed;
    z-index: 1001;
    background: #f2f3f7;
    -webkit-transition: 0.5s;
    transition: 0.5s;
    display: block;
    height: -webkit-fill-available;
  }
  .menu-collapse{
      display: block;
      margin-left:280px;
      position: fixed;
      z-index: 1010;
      color:black;
      cursor: pointer;
  }
  .menu-open{
      color:black;
      font-size: 1.5em;
      position: fixed;
      cursor: pointer;
  }
  .content {
    width: calc(100% - 20px);
    float: right;
    -webkit-transition: 0.5s;
    transition: 0.5s;    
    display: block;
  }

  .font-note{
    font-family: "Quicksand", Arial, sans-serif;
    color: rgba(0, 0, 0, 0.7);
    font-size: 12px;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 1px;
  }
  @media (min-width:768px){
      .menu-collapse, .menu-open{
          display: none;
      }
      .content{
          width:calc(100% - 320px);
      }
  }
section{
    margin:50px 0;
    padding-right: 20px;
}
.welcome-banner{
    background-color: rgba(0, 0, 0, 0.7);
    color:white;
    border-radius: 0 30px 30px 0;
}
.btn-outline-secondary{
    color: white;
    border-color: white;
}
.section-header{
    margin-bottom: 30px;
}
.panel-heading{
    padding: 0;
    min-height: 50px;
    background-color: #b9c4e2;
    color: #fff;   
}
.panel-title{
    display: block;
    background-color: #b9c4e2;
    color: #fff;   
    font-weight: 500;
    font-size: 14px;
    letter-spacing: 1px;
    text-transform: uppercase;
    font-family: "Quicksand", Arial, sans-serif;
}
.panel-title button{
    display: block;
    padding: 15px 15px;
    color: #fff;    
    text-transform: uppercase;
    text-align: start;
}
.panel-body{
    padding: 10px;
    margin-top: -5px;
    border: 1px solid #e6e6e6 !important;
}
.panel-content{
    font-size: 13.5px;
}

.timeline ul{
    padding-left:20px;
    list-style-type:none;
}

.timeline li p{
    margin-left: 18px;
}

.timeline ul li:before {
    content: '\2713';
    float: left;
  }
.section-content .container{
    margin: 10px 0;
}
.section-content .row{
    margin-top: 20px;
}
.progress-bar, .progress{
    height: 10px;
}
.progress-bar.color-1{
    background-color:#f3f3c1;
}
.progress-bar.color-2{
    background-color:#efdcd4;
}
.progress-bar.color-3{
    background-color:#d4efd5;
}
.progress-bar.color-4{
    background-color:#dfd4ef;
}

.avatar{
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    position: relative;
    width: 150px;
    height: 150px;
    margin: 0 auto;
    margin-bottom: 20px;
    border-radius: 50%;
}

.side h1{
    color: #000;
    font-family: "Playfair Display", Georgia, serif;
    font-weight: 400;
    margin: 0;
    font-weight: 700;
    font-size: 22px;
    display: block;
    width: 100%;
}

.navbar{
    display: block;
    width: 100%;
    margin-top: 30px;
}

.navbar ul, .footer ul{
    text-align: center;
    margin: 0;
    padding: 0;
}

.navbar ul li{
    margin: 0 0 10px 0;
    padding: 0;
    list-style: none;
    font-size: 12px;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 1px;
}
.navbar ul li.active a {
    color: #b9c4e2;
}
.navbar ul li a,.footer ul li a {
    color: rgba(0, 0, 0, 0.7);
    text-decoration: none;
    position: relative;
    padding: 10px 0;
    font-family: "Quicksand", Arial, sans-serif;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}

.side .footer{
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
}

.footer li{
    display: inline;
    margin: 0 5px;
}
.footer .fa-2x{
    font-size:1.2em;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

